.wrap { 
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: -webkit-linear-gradient(85deg, #fb63f9, #c2e534);
    background: linear-gradient(85deg, #fb63f9, #c2e534);
}


.wrap_session {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: -webkit-linear-gradient(147deg, #04e772,#1ac80e,#5ddf9c);
    background: linear-gradient(147deg, #04e772,#1ac80e,#5ddf9c);
}

.wrap_break {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: beige;
    background: -webkit-linear-gradient(46deg, #370606,#e00b0b,#5e181f); 
    background: linear-gradient(46deg, #370606,#e00b0b,#5e181f);                                         
}

.work_time {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-bottom: 30px;
}

.main_time {
    font-size: 100px;
}

.main_status {
    font-size: 30px;
}

.time {
    width: 30%;
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    margin-bottom: 15px;
    font-size: 20px;
}

.time_container {
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.time_button {
    display: flex;
    margin: 0 15px;
    padding: 1px 2px 2px 5px;
    width: 25px;
    height: 25px;
    border: 3px solid black;
    font-weight: bold;
    background-color: unset;
    cursor: pointer;
}

.time_button_break {
    display: flex;
    margin: 0 15px;
    padding: 1px 2px 2px 5px;
    width: 25px;
    height: 25px;
    border: 3px solid beige;
    color: beige;
    font-weight: bold;
    background-color: unset;
    cursor: pointer;
}

.time_item {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.control_button {
    margin: 4px 6px;
    border: 3px solid black;
    font-family: 'Silkscreen', cursive;
    background-color: unset;
}

.control_button:disabled {
    border-color: rgba(0, 0, 0, 0.104);
}

@media (max-width: 1350px){
    .time {
        width: 35%;
    }
}

@media (max-width: 1150px){
    .time {
        width: 40%;
    }
}

@media (max-width: 998px){
    .time {
        width: 45%;
    }
}

@media (max-width: 890px){
    .time {
        width: 70%;
    }
}

@media (max-width: 580px){
    .main_status {
        font-size: 25px;
    }

    .main_time {
        font-size: 80px;
    }

    .time {
        width: 100%;
    }

    .time_button {
        margin: 0 10px;
        padding: 0px 0px 0px 3px;
        width: 20px;
        height: 20px;
    }

    .time_button_break {
        margin: 0 10px;
        padding: 0px 0px 0px 3px;
        width: 20px;
        height: 20px;
    }

    .time_item {
        font-size: 18px;
    }
}

@media (max-width: 321px){
    .time_item {
        font-size: 14px;
    }
}
